import React, { useRef } from 'react';
import { Button, Modal, Form, Input, InputNumber, Select } from 'antd';
import UploadCustom from 'common/components/uploadCommon/UploadCustom'

export const DEGREE_OPTION = [
	{ value: 0, label: "Trung Cp" },
	{ value: 1, label: "Cao ng" },
	{ value: 2, label: "C nhn" },
	{ value: 3, label: "Thc s" },
	{ value: 4, label: "Tin s" },
];

const DegreeModal = ({ formInstance, type = 'update', isOpen = false, onClose, isLoading, onFinish }) => (
  
  <Modal
    title={type === 'update' ? 'Thm bng cp' : 'Chnh sa bng cp'}
    open={isOpen}
    form={formInstance}
    onCancel={onClose}
    footer={[
      <Button form="degreeForm" key="back" onClick={onClose}>
        Hy
      </Button>,
      <Button form="degreeForm" key="submit" type="primary" htmlType="submit" loading={isLoading}>
        Lu
      </Button>,
    ]}
    destroyOnClose={true}
  >
    <Form id="degreeForm" form={formInstance} layout="vertical" onFinish={onFinish}>
      <Form.Item hidden={true} name="employeeId">
        <Input />
      </Form.Item>
      <Form.Item hidden={true} name="id">
        <Input />
      </Form.Item>
      <Form.Item
        label="Tn bng cp"
        name="name"
        rules={[{ required: true, message: 'Tn bng cp khng c b trng!' }]}
      >
        <Select
          // defaultValue="lucy"
          style={{ width: 120 }}
          // onChange={handleChange}
          options={DEGREE_OPTION}
        />
      </Form.Item>
      <Form.Item label="Chuyn ngnh" name="major">
        <Input />
      </Form.Item>
      <Form.Item label="Xp loi" name="levelOfTraining">
        <Input />
      </Form.Item>
      <Form.Item label="Hnh thc o to" name="formOfTraining">
        <Input />
      </Form.Item>
      <Form.Item label="Nm tt nghip" name="yearOfGraduation">
        <InputNumber min={1971} />
      </Form.Item>
      <Form.Item label="Trng o to" name="university">
        <Input />
      </Form.Item>
      <Form.Item label="S hiu trng" name="numberOfUniversity">
        <InputNumber min={1971} />
      </Form.Item>
      <Form.Item hidden={true} name="degreeFile">
        <Input />
      </Form.Item>
      <Form.Item label="Ti liu" name="degreeFileId">
        <UploadCustom form={formInstance} nameField="degreeFileId" fileObject="degreeFile" fileObjectName="degreeFile"/>
      </Form.Item>
    </Form>
  </Modal>
);
DegreeModal.propTypes = {};
export default DegreeModal;
